<template>
  <div class="describe">
        <div class="top">
            <dl v-for="(item,index) in toplist" :key="index">
                <dt><img :src='item.src' alt=""></dt>
                <dd>
                    <h5>{{ item.title }}</h5>
                    <p>{{ item.text }}</p>
                </dd>
            </dl>
        </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      toplist: [
        {
          'src': require('../assets/images/ad_01.png'),
          'title': '',
          'text': '100%'
        },
        {
          'src': require('../assets/images/ad_02.png'),
          'title': '',
          'text': '01'
        },
        {
          'src': require('../assets/images/ad_03.png'),
          'title': '',
          'text': ''
        }
      ]
    }
  }
}
</script>

<style lang="stylus" scoped>
.describe
  width 100%
  background #fff
  box-sizing content-box
  margin-bottom 30px
  .top
    width 1200px
    margin 0 auto
    dl
      width 340px
      display inline-block
      text-align center
      margin 100px 80px 100px 0
    dl:nth-child(3)
      margin-right 0
    dt
      img
        height 96px
    dd
      h5
       font-size 18px;
       color #333;
       padding 20px 0 15px 0
      p
       font-size 14px
       color #666 
</style>



