<template>
  <div class="container">
      <img src="../assets/images/title-case.png" alt="">
      <p></p>
      <ul class="clearfix" v-for="(item,index) in caselist" :key="index">
          <li :src='item.src' :class='item.layout'>
            <div class="textarea">
              <h3>{{ item.title }}</h3>
              <p>{{ item.text }}</p>
            </div>
              <img :src='item.src' alt="" >
          </li>
      </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      caselist: [
        {
          src: require('../assets/images/case-2.jpg'),
          title: '8H',
          text: '8H6102000%',
          layout: 'left'
        },
        {
          src: require('../assets/images/case-1.jpg'),
          title: '',
          text: 'appAPP',
          layout: 'right'
        }
      ]
    }
  }
}
</script>

<style lang="stylus" scoped>
.container
  width 1200px
  padding 90px 0 60px
  margin 0 auto
  background #fff
img
  display: block;
  margin: 0 auto;
p
  font-size: 14px;
  color: #7c7c7c;
  text-align: center;
  line-height: 25px;
  margin-top: 15px;
ul
  margin:80px 90px 0px
  li
    margin-bottom 50px
    padding-left 2%
    .textarea
      width 33%
      text-align center
      padding-top 100px
    img
      width 60%
.left
  .textarea
    float left
  img 
    float right
    margin-right -60px 
.right
  .textarea
    float right
  img 
    float left
    margin-left -60px    
</style>





